export default {
    methods: {
        khmerDateFormat(date) {
            let newDate = new Date(date);
            newDate.setTime(newDate.getTime() + (7 * 60 * 60 * 1000));
            const day = newDate.getDate();
            const month = newDate.getMonth();
            const year = newDate.getFullYear();

            return ` ${this.formatDateNumber(day)}  ${this.getKhmerMonth(month)}  ${year}`;
        },
        shortKhmerDateFormat(date) {
            const newDate = new Date(date);
            const day = newDate.getDate();
            const month = newDate.getMonth();
            const year = newDate.getFullYear();
            const minute = newDate.getMinutes();
            const hour = newDate.getHours();
            const amPm = hour >= 12 ? '' : '';

            return `${this.formatDateNumber(day)}-${this.getKhmerMonth(month)}-${year} | ${this.formatDateNumber(hour % 12)}:${this.formatDateNumber(minute)} ${amPm}`;
        },
        formatDateNumber(value) {
            return value.toString().padStart(2, '0');
        },
        getKhmerMonth(month) {
            const newMonth = `${month + 1}`;
            switch (newMonth) {
                case "1":
                    return "";
                case "2":
                    return "";
                case "3":
                    return "";
                case "4":
                    return "";
                case "5":
                    return "";
                case "6":
                    return "";
                case "7":
                    return "";
                case "8":
                    return "";
                case "9":
                    return "";
                case "10":
                    return "";
                case "11":
                    return "";
                case "12":
                    return "";
            }
            return "";
        },
        toYmdDate(date) {
            const month = this.pad2(date.getMonth() + 1);//months (0-11)
            const day = this.pad2(date.getDate());//day (1-31)
            const year = date.getFullYear();

            return year + "-" + month + "-" + day;
        },
        pad2(n) {
            return (n < 10 ? '0' : '') + n;
        },
        humanDate(dateTime) {
            dateTime = new Date(dateTime);
            dateTime.setTime(dateTime.getTime() + (7 * 60 * 60 * 1000));
            const hdate = require('human-date');
            let result = hdate.relativeTime(dateTime);
            result = result.replace("seconds", "");
            result = result.replace("second", "");
            result = result.replace("minutes", "");
            result = result.replace("minute", "");
            result = result.replace("hours", "");
            result = result.replace("hour", "");
            result = result.replace("days", "");
            result = result.replace("day", "");
            result = result.replace("weeks", "");
            result = result.replace("week", "");
            result = result.replace("months", "");
            result = result.replace("month", "");
            result = result.replace("years", "");
            result = result.replace("year", "");
            result = result.replace(" ago", "");
            return result;
        }

    }
}
